import React from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";

const AuthForm = () => (
  <Formik
    initialValues={{ email: "", password: "", name: "", radio: "sign in" }}
    onSubmit={console.log}
  >
    <div>
      <h1>Any place in your app!</h1>
      <Form>
        <Field name="email" />
        <Field name="password" />
        <label>
          <Field type="radio" name="radio" value="sign up" />
          Sign up
        </label>
        <label>
          <Field type="radio" name="radio" value="sign in" />
          Sign in
        </label>
        <Field name="name" />
        <button type="submit">Submit</button>
      </Form>
    </div>
  </Formik>
);

export default AuthForm;
